.profile__avatar-overlay {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -o-object-fit: scale-down;
  object-fit: scale-down;
}

.profile__avatar-overlay:hover {
  opacity: 1;
  cursor: pointer;
}